<template>
  <div class="pf-app">
    <a class="link-back" href="https://www.matekliku.cz/"
      >« vrátit se zpět na web matekliku.cz</a
    >
    <img class="hills" src="@/assets/Hills-and-trees.png" />
    <div class="main-container">
      <div class="left">
        <img class="odpalte" src="@/assets/text-odpalte.png" alt="" />
        <div class="persons">
          <h3 class="list-heading">Oblohu rozzářili:</h3>
          <div class="container">
            <ul class="person-list">
              <li v-for="(person, index) in persons" :key="index">
                {{ person }}
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="right">
        <img class="logo" src="@/assets/cobra_logo-png_negative.png" />
        <img class="pf" src="@/assets/PF2021.png" alt="" />
      </div>
    </div>
    <div class="candles">
      <img
        @click="action(1)"
        class="candle c-1"
        :src="require(`@/assets/green_${firework === 1 ? 'on' : 'off'}.gif`)"
        alt=""
      />
      <img
        @click="action(2)"
        class="candle  c-2"
        :src="require(`@/assets/pink_${firework === 2 ? 'on' : 'off'}.gif`)"
        alt=""
      />
      <img
        @click="action(3)"
        class="candle c-3"
        :src="require(`@/assets/blue_${firework === 3 ? 'on' : 'off'}.gif`)"
        alt=""
      />

      <img
        @click="action(4)"
        class="candle c-4"
        :src="require(`@/assets/yellow_${firework === 4 ? 'on' : 'off'}.gif`)"
        alt=""
      />
    </div>
    <div v-if="modal1" class="modal">
      <div v-if="!submited">
        <div>
          <p class="mdl-text bold">Zadejte vaše jméno nebo název společnosti</p>
          <p class="mdl-text">
            Zapíšeme vás do seznamu těch, kteří nám pomohli oblohu rozzářit.
          </p>
        </div>
        <form @submit.prevent="nameInserted">
          <input v-model="nameInput" class="mdl-inpt" type="text" />
          <button class="mdl-btn" type="submit">POTVRDIT</button>
        </form>
      </div>
      <div v-else>
        <h1 class="mdl-text">{{ nameInput }}</h1>
        <p class="mdl-text">Přejeme Vám krásné Vánoce a šťastný nový rok</p>
        <img class="mdl-logo" src="@/assets/thx_logo.jpg" alt="" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PF',

  data() {
    return {
      modal1: false,
      submited: false,
      firework: 0,
      nameInput: '',
      done: false,
      persons: [
        'Jiřinka Loubková',
        'Rozálie Křížová',
        'Martin Pohl',
        'Luděk Hušek',
        'Ladislav Kolář',
        'Ivan Lintner',
        'Božena Strojilová',
        'Pavel Hynek',
        'Miroslav Mádle',
        'Tibor Varaďa',
      ],
    };
  },
  methods: {
    action(num) {
      if (!this.done) {
        this.done = true;
        console.log('spouštím', num);
        this.firework = num;
        setTimeout(() => {
          this.modal1 = !this.modal1;
        }, 1500);
      }
    },
    nameInserted() {
      if (this.nameInput.length > 0) {
        this.persons.push(this.nameInput);
        this.submited = true;
        setTimeout(() => {
          this.modal1 = false;
          this.nameInput = '';
          this.submited = false;
        }, 5000);
      }
    },
  },
};
</script>

